<template lang="html">
  <div class="_full_router setProblemBody">
    <custHeader :title="title"></custHeader>
    <div v-show="flowId==1">
      <mt-field  type="password" label="交易密码" placeholder="请输入交易密码" v-model="password"></mt-field>
      <mt-button  type="primary" size="large"  @click="checkPass">下一步</mt-button>
    </div>
    <div v-show="flowId==2">
      <div class="" @click="showQues(1)">
        <mt-field label="安保问题" v-model="ques1"></mt-field>
      </div>

      <mt-field label="答案" v-model="answer1"></mt-field>
      <div class="" @click="showQues(2)">
        <mt-field label="安保问题" v-model="ques2"></mt-field>
      </div>

      <mt-field label="答案" v-model="answer2"></mt-field>
      <div class="" @click="showQues(3)">
        <mt-field label="安保问题" v-model="ques3"></mt-field>
      </div>

      <mt-field label="答案" v-model="answer3"></mt-field>
      <mt-button type="danger" class="setBtn" @click="set" name="button" >提交</mt-button>
    </div>
    <!-- <mt-field  type="passwordNew" label="交易密码" v-model="password" v-show=false></mt-field> -->

    <mt-popup
        v-model="quesVisible"
        style="width:100%"
        position="bottom"
      >
        <button class="btn" @click="dissQues">取消</button>
        <button class="btn" @click="sureQues">确认</button>
        <mt-picker :slots="quesSlots" class="" valueKey="name" @change="quesChange" :visible-item-count="3"></mt-picker>
      </mt-popup>

  </div>
</template>

<script>
import custHeader from  'components/custHeader'
import {MessageBox} from 'mint-ui'
export default {
  data(){
    return {
      title:"设置安保问题",
      answer1:'',
      answer2:'',
      answer3:'',
      ques1:'',
      ques2:'',
      ques3:'',
      ques1value:'0',
      ques2value:'1',
      ques3value:'2',
      quesVisible:false,
      quesFlag:1,
      flowId:2,
      quesSlots:[{
        flex:2,
        // values:[{value:'0',name:'你的小学名字'},{value:'1',name:'你的第一辆车'},{value:'2',name:'父亲姓名'}],
        values:[],
        className:"slots"
      }],
      isChange:false,
      password:''
    }
  },
  created:function(){
    var self = this;
    window.dict("SECURITY_QUES").then(data => {
      var data = data;
      for (var i = 0; i < data.length; i++) {
        self.quesSlots[0].values.push({value:data[i].ddItem,name:data[i].ddItemName});
      }
      self.ques1 = self.quesSlots[0].values[0].name
      self.ques1value = self.quesSlots[0].values[0].value
      self.ques2 = self.quesSlots[0].values[1].name
      self.ques2value = self.quesSlots[0].values[1].value
      self.ques3 = self.quesSlots[0].values[2].name
      self.ques3value = self.quesSlots[0].values[2].value
    })


    axiosUtil.qrySafeQuestion().then(data => {
      var data = data.data;
      if(data.data !== undefined){
        self.isChange = true
        self.flowId = 1;
        self.title = "重置安保问题"
      }else{

      }
    })

  },
  methods:{
    checkPass:function(){
        var self = this;
        axiosUtil.upCltPasswordCheck({useScope:'1',authType:'1',password:self.password}).then(data => {
          var data = data.data
          if(data.errorcode == 0 ){
            self.flowId = 2;
          }else{
            MessageBox.alert(data.errormsg)
          }
        })
    },
    set:function(){
      var quesData = {
        qustion1:this.ques1value,answer1:this.answer1,
        qustion2:this.ques2value,answer2:this.answer2,
        qustion3:this.ques3value,answer3:this.answer3,
      }
      if (global.isOpenMd5) {
        this.password = global.md5(this.password);
      }
      if(this.isChange){
        quesData.password = this.password
      }
      if(this.answer1 && this.answer2 && this.answer3){
        axiosUtil.setSafeQuestion(quesData).then(data => {
          var data = data.data;
          if(data.errorcode == 0){
            if(!this.isChange){
              MessageBox.confirm("设置安保成功，是否去实名认证").then(action => {
                router.push("/personal/realnameAuth")
              },action => {
                router.push("/personal")
              })
            }else{
              MessageBox.alert("重置安保成功").then(action => {
                router.push("/personal")
              })
            }
            
          }else{
            MessageBox.alert("设置失败<br>"+data.errormsg)
          }
        })
      }else{
        MessageBox("请将答案填写完整！")
      }
    },
    dissQues:function(){
      this.quesVisible = false
    },
    sureQues:function(){
      this.quesVisible = false
    },
    showQues:function(id){
      this.quesVisible = true
      this.quesFlag = id
    },
    quesChange:function(picker,value){
      if(this.quesFlag == 1) {
        this.ques1 = value[0].name
        this.ques1value =value[0].value
      }else if(this.quesFlag == 2){
        this.ques2 = value[0].name
        this.ques2value =value[0].value
      }else{
        this.ques3 = value[0].name
        this.ques3value =value[0].value
      }
    }
  },
  components:{
    custHeader
  },
}
</script>

<style lang="css">
.setProblemBody{
    background-color: #eee;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 5;
}
.btn{
  width: 49%;
  background-color: #fff;
  border: 0;
  height: 40px;
  font-size: 20px;
  outline: 0;
}
.setBtn{
  width: 100%;
  position: fixed;
  bottom: 0%;
}
</style>



